<template>
  
  <el-dropdown class="user-avatar-wrapper" @command="handleCommand">
    <div class="avatar-box">
      <el-avatar size="small" :src="this.HOST+'/'+this.userInfo.avatar" />
      <span class="username">{{userInfo.username}}</span>
      <i class="el-icon-caret-bottom" />
    
    </div>
 
    <el-dropdown-menu slot="dropdown">
      <el-dropdown-item command="userCenter"></el-dropdown-item>
      <el-dropdown-item command="loginOut"></el-dropdown-item>
    </el-dropdown-menu>
    
  </el-dropdown>
</template>

<script>

export default {
  inject: ['userInfo'],
  created() {
    // 
   
  },
  name: 'UserAvatar',
  data() {
    return {
      // userInfo: {},
      imgBaseUrl: this.HOST,
    }
  },
  methods: {
    handleCommand(command) {
      if (command === 'userCenter') {
          this.$router.push({ path: '/personInfo' })
      }
      if (command === 'loginOut') {
          this.loginOut()
      }
    },
    loginOut() {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        sessionStorage.removeItem("user")
        this.$router.push({ path: '/login'});
        //   removeToken()
        //   location.reload()
      })
    },

  }
}
</script>

