              class="count"
              :start-val="0"
              :end-val="statistics.orderCount"
              :duration="3000"
            />
            <p class="title"></p>
            <p class="sub-title">: {{ statistics.totalAmount }}</p>
          </div>
        </div>
      </el-card>

      <!--  -->
      <el-card shadow="hover" :body-style="{ padding: '0px' }" class="data-col">
        <div class="date-block">
          <i
            class="el-icon-house icon-box"
            style="background: linear-gradient(45deg, #9a66e4, #b794f4)"
          />
          <div class="date-cont">
            <CountTo
              class="count"
              :start-val="0"
              :end-val="statistics.fosterCount"
              :duration="3000"
            />
            <p class="title"></p>
            <p class="sub-title">
              : {{ statistics.currentFosterCount }}
            </p>
          </div>
        </div>
      </el-card>
    </div>

    <!--  -->
    <div class="charts-container">
      <el-row :gutter="20">
        <!--  -->
        <el-col :span="7">
          <el-card shadow="hover" class="chart-card">
            <div slot="header" class="chart-header">
              <span></span>
            </div>
            <div id="animalStatusChart" class="chart-wrapper"></div>
          </el-card>
        </el-col>

        <!--  -->
        <el-col :span="10">
          <el-card shadow="hover" class="chart-card middle-chart">
            <div slot="header" class="chart-header">
              <span>1</span>
            </div>
            <div id="adoptTrendChart" class="chart-wrapper"></div>
          </el-card>
        </el-col>

        <!--  -->
        <el-col :span="7">
          <el-card shadow="hover" class="chart-card">
            <div slot="header" class="chart-header">
              <span></span>
            </div>
            <div id="animalBreedChart" class="chart-wrapper"></div>
          </el-card>
        </el-col>
      </el-row>
    </div>

    <!--  -->
    <div class="bottom-container">
      <el-row :gutter="20">
        <el-col :span="24">
          <el-card shadow="hover" class="notice-card">
            <div slot="header" class="notice-header">
              <span></span>
            </div>
            <div class="notice-content">
              <el-timeline>
                <el-timeline-item
                  v-for="(announcement, index) in announcements"
                  :key="index"
                  :timestamp="announcement.time"
                  placement="top"
                  :type="index === 0 ? 'primary' : ''"
                  :color="index === 0 ? '#2d8cf0' : ''"
                >
                  <el-card class="announcement-card">
                    <h4 class="announcement-title">{{ announcement.title }}</h4>
                    <p class="announcement-text">{{ announcement.content }}</p>
