<script>
import { Timeline, TimelineItem, Card, Button } from "element-ui";
import CountTo from "vue-count-to";
import Request from "../../../utils/request.js";
import * as echarts from "echarts";

export default {
  name: "HomeView",
  components: {
    [Card.name]: Card,
    [Timeline.name]: Timeline,
    [TimelineItem.name]: TimelineItem,
    [Button.name]: Button,
    CountTo,
  },
  data() {
    return {
      statistics: {
        animalCount: 0,
        availableCount: 0,
        userCount: 0,
        newUserCount: 0,
        adoptCount: 0,
        monthAdoptCount: 0,
        orderCount: 0,
        totalAmount: 0,
        fosterCount: 0,
        currentFosterCount: 0,
      },
      noticeLimit: 10,
      chartData: {
        columns: ["", ""],
        rows: [
          { : "11", : 120 },
          // 
        ],
      },
      chartSettings: {
        // 
      },
      announcements: [],
      adoptTrendChart: null,
      animalStatusChart: null,
      animalBreedChart: null,
    };
  },
  created() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      // 
      Request.get("/show/getStatistics").then((response) => {
        if (response.code === "0") {
          this.statistics = response.data;
        }
      });

      // 
      this.fetchChartData();

      // 
      this.fetchNotices();
    },
    fetchNotices() {
      // NoticeControllerlimit
      Request.get("/notice/limit", {
        params: {
          count: this.noticeLimit,
        },
      }).then((response) => {
        if (response.code === "0") {
          this.announcements = response.data;
        }
      });
    },
    fetchChartData() {
      // 
      Request.get("/show/getAdoptTrend").then((response) => {
        if (response.code === "0") {
          this.initAdoptTrendChart(response.data);
        }
      });

      // 
      Request.get("/show/getAnimalStatus").then((response) => {
        if (response.code === "0") {
          this.initAnimalStatusChart(response.data);
        }
      });

      // 
      Request.get("/show/getAnimalBreed").then((response) => {
        if (response.code === "0") {
          this.initAnimalBreedChart(response.data);
        }
      });
    },
    initAdoptTrendChart(data) {
      const chartDom = document.getElementById("adoptTrendChart");
      if (!chartDom) return;

      if (this.adoptTrendChart) {
        this.adoptTrendChart.dispose();
      }

      this.adoptTrendChart = echarts.init(chartDom);

      const dates = data.map((item) => item.date);
      const counts = data.map((item) => item.count);

      const option = {
        title: {
          text: "1",
        },
        tooltip: {
          trigger: "axis",
          formatter: function (params) {
            const param = params[0];
            return `${param.name}<br/>${param.value}`;
          },
        },
        xAxis: {
          type: "category",
          data: dates,
          axisLabel: {
            rotate: 45,
            interval: "auto",
            formatter: function (value) {
              return value.substring(5); // -
            },
          },
        },
        yAxis: {
          type: "value",
          name: "",
        },
        series: [
          {
            name: "",
            data: counts,
            type: "line",
            smooth: true,
            areaStyle: {
              opacity: 0.3,
            },
            itemStyle: {
              color: "#2d8cf0",
            },
          },
        ],
      };

      this.adoptTrendChart.setOption(option);
    },
    initAnimalStatusChart(data) {
      const chartDom = document.getElementById("animalStatusChart");
      if (!chartDom) return;

      if (this.animalStatusChart) {
        this.animalStatusChart.dispose();
      }

      this.animalStatusChart = echarts.init(chartDom);

      const option = {
        title: {
          text: "",
          left: "center",
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)",
        },
        legend: {
          orient: "vertical",
          left: "left",
          top: "middle",
          itemWidth: 10,
          itemHeight: 10,
          textStyle: {
            fontSize: 12,
          },
        },
        series: [
          {
            name: "",
            type: "pie",
            radius: ["40%", "70%"],
            center: ["60%", "50%"],
            avoidLabelOverlap: true,
            itemStyle: {
              borderRadius: 10,
              borderColor: "#fff",
              borderWidth: 2,
            },
            label: {
              show: true,
              position: "outside",
              formatter: "{b}: {c}",
            },
            emphasis: {
              label: {
                show: true,
                fontSize: 14,
                fontWeight: "bold",
              },
            },
            labelLine: {
              show: true,
              length: 10,
              length2: 10,
              smooth: true,
            },
            data: data.map((item) => ({
              name: item.status,
              value: item.count,
            })),
          },
        ],
      };

      this.animalStatusChart.setOption(option);
    },
    initAnimalBreedChart(data) {
      const chartDom = document.getElementById("animalBreedChart");
      if (!chartDom) return;

      if (this.animalBreedChart) {
        this.animalBreedChart.dispose();
      }

      this.animalBreedChart = echarts.init(chartDom);

      const option = {
        title: {
          text: "",
          left: "center",
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)",
        },
        legend: {
          orient: "vertical",
          left: "left",
          top: "middle",
          itemWidth: 10,
          itemHeight: 10,
          textStyle: {
            fontSize: 12,
          },
        },
        series: [
          {
            name: "",
            type: "pie",
            radius: ["40%", "70%"],
            center: ["60%", "50%"],
            avoidLabelOverlap: true,
            itemStyle: {
              borderRadius: 10,
              borderColor: "#fff",
              borderWidth: 2,
            },
            label: {
              show: true,
              position: "outside",
              formatter: "{b}: {c}",
            },
            emphasis: {
              label: {
                show: true,
                fontSize: 14,
                fontWeight: "bold",
              },
            },
            labelLine: {
              show: true,
              length: 10,
              length2: 10,
              smooth: true,
            },
            data: data.map((item) => ({
              name: item.breed,
              value: item.count,
            })),
          },
        ],
      };

      this.animalBreedChart.setOption(option);
    },
  },
  mounted() {
    this.fetchData();
    // 
    window.addEventListener("resize", () => {
      this.adoptTrendChart?.resize();
      this.animalStatusChart?.resize();
      this.animalBreedChart?.resize();
    });
  },
  beforeDestroy() {
    // 
    this.adoptTrendChart?.dispose();
    this.animalStatusChart?.dispose();
    this.animalBreedChart?.dispose();
    window.removeEventListener("resize", this.handleResize);
  },
};
</script>

