<template>
    <div class="category-wrapper">
        <el-card shadow="always">
            <!--  -->
            <div class="control-btns">
                <el-popconfirm title="?" @confirm="batchDelete">
                    <template #reference>
                        <el-button type="danger" size="medium"
                            style="background-color: red;border-color: red;"></el-button>

                    </template>
                </el-popconfirm>

                <el-button type="primary" size="medium" @click="handleCreate"
                    style="float:right;background-color: blue;border-color: blue;"></el-button>

                <el-button type="primary" size="medium" @click="exportVisible = true"
                    style="float:right;margin-right: 10px;background-color: blue;border-color: blue;"></el-button>

            </div>
            <!--  -->
            <el-form ref="searchForm" :inline="true" :model="listQuery" label-width="90px" class="search-form">
                <el-form-item label="">
                    <el-input v-model="listQuery.name" placeholder="" />
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" size="medium" style="background-color: blue;border-color: blue;"
                        @click="onSubmit"></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="danger" size="medium" style="background-color: red;border-color: red;"
                        @click="onReset"></el-button>
                </el-form-item>
            </el-form>
            <!--  -->
            <el-table ref="multipleTable" v-loading="listLoading" :data="tableData" tooltip-effect="dark" row-key="id"
                style="width: 100%" size="medium" @selection-change="handleSelectionChange" border default-expand-all>
                <el-table-column type="selection" width="50"></el-table-column>
                <el-table-column prop="id" label="id"></el-table-column>
                <el-table-column prop="name" label=""></el-table-column>
                <el-table-column prop="path" label=""></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <i :class="scope.row.icon" />
                    </template>
                </el-table-column>
                <el-table-column prop="description" label=""></el-table-column>
                <el-table-column prop="pagePath" label=""></el-table-column>
                <el-table-column prop="role" label="">
                    <template slot-scope="scope">
                        <div>{{ getMenuRole(scope.row.role) }}</div>
                    </template>
                </el-table-column>
                <el-table-column prop="sortNum" label=""></el-table-column>
                <el-table-column label="" width="350">
                    <template slot-scope="scope">
                        <el-button size="small" type="success" class="el-icon-circle-plus"
                            @click="addChildMenu(scope.row.id)"
                            v-if="!scope.row.pid && !scope.row.path"></el-button>
                        <el-button size="small" type="primary" class="el-icon-edit"
                            @click="handleEdit(scope.row)"></el-button>
                        <el-button size="mini" type="danger"
                            @click="handleDelete(scope.$index, scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!-- /  -->


            <el-dialog title="" :visible.sync="formVisibleEdit" width="30%" :modal="false">
                <el-form :model="dialogForm" :rules="formRules" ref="dialogForm">
                    <el-form-item label="" :label-width="formLabelWidth" prop="name">
                        <el-input v-model="dialogForm.name" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input v-model="dialogForm.path" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth" prop="icon">
                        <el-select v-model="dialogForm.icon" filterable placeholder="">
                            <el-option v-for="dict in iconDict" :key="dict.itemKey" :label="dict.itemKey"
                                :value="dict.itemValue">
                                <i :class="dict.itemValue"></i>&nbsp;&nbsp;{{ dict.itemKey }}
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input v-model="dialogForm.pagePath" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth" prop="sortNum">
                        <el-input v-model="dialogForm.sortNum" autocomplete="off" type="number"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth" prop="role">
                        <el-select v-model="dialogForm.role" placeholder="">
                            <el-option label="" :value="0"></el-option>
                            <el-option label="" :value="2"></el-option>
                            <el-option label="" :value="1"></el-option>
                            <el-option label="" :value="3"></el-option>

                        </el-select>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input v-model="dialogForm.description" autocomplete="off"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="formVisibleEdit = false"> </el-button>
                    <el-button type="primary" :disabled="isSubmit" @click="handleEditSave('dialogForm')"> </el-button>
                </div>
            </el-dialog>

            <el-dialog title="" :visible.sync="formVisibleAdd" width="30%">
                <el-form :model="dialogForm" :rules="formRules" ref="dialogForm">
                    <el-form-item label="" :label-width="formLabelWidth" prop="name">
                        <el-input v-model="dialogForm.name" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input v-model="dialogForm.path" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth" prop="icon">
                        <el-select v-model="dialogForm.icon" filterable placeholder="">
                            <el-option v-for="dict in iconDict" :key="dict.itemKey" :label="dict.itemKey"
                                :value="dict.itemValue">
                                <i :class="dict.itemValue"></i>&nbsp;&nbsp;{{ dict.itemKey }}
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input v-model="dialogForm.pagePath" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth" prop="sortNum">
                        <el-input v-model="dialogForm.sortNum" autocomplete="off" type="number"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth" prop="role">
                        <el-select v-model="dialogForm.role" placeholder="">
                            <el-option label="" :value="0"></el-option>
                            <el-option label="" :value="2"></el-option>
                            <el-option label="" :value="1"></el-option>
                            <el-option label="" :value="3"></el-option>

                        </el-select>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input v-model="dialogForm.description" autocomplete="off"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="formVisibleAdd = false"> </el-button>
                    <el-button type="primary" :disabled="isSubmit" @click="handleAdd('dialogForm')"> </el-button>
                </div>
            </el-dialog>

            <!--   -->
            <el-dialog title="" :visible.sync="exportVisible" width="500px">
                <div class="export-data" style="display: flex;flex-direction: row;justify-content: space-between;">
                    <el-button type="primary" @click="exportTable('xlsx')">EXCEL</el-button>
                    <el-button type="primary" @click="exportTable('csv')">CSV</el-button>
                    <el-button type="primary" @click="exportTable('txt')">TXT</el-button>
                </div>
                <div class="hints">TIP</div>
            </el-dialog>
        </el-card>
    </div>
</template>

<script>
import excel from '../utils/excel.js'
import Pagination from '../components/Pagination/index.vue'
import Upload from '../components/Upload/index.vue'
import Hints from '../components/Hints/index.vue'
import Request from '../utils/request.js'
export default {
    name: 'Menu',
    components: { Pagination, Upload, Hints },
    data() {
        return {
            // 
            listLoading: true,
            formLabelWidth: '80px',
            iconDict: {},
            // 
            listQuery: {
                name: undefined
            },
            // /
            dialogForm: {
                name: undefined,
                description: undefined,
            },

            // 
            total: 0,
            // 
            tableData: [],
            // 
            multipleSelection: [],
            // / /
            formVisible: false,
            formVisibleAdd: false,
            formVisibleEdit: false,
            //   
            //  trigger: ['blur', 'change'] 
            formRules: {
                // 
                name: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                sortNum: [
                    { required: true, message: '', trigger: 'blur' },

                ],
                role: [
                    { required: true, message: '', trigger: 'change' }
                ],
                icon: [
                    { required: true, message: '', trigger: 'change' }
                ]
            },
            // 
            isSubmit: false,

            // 
            filesFormat: '.txt, .csv, .xls, .xlsx',
            //  /
            exportVisible: false
        }
    },
    created() {
        this.fetchData()
    },
    methods: {
        // 
        handleSelectionChange(val) {
            this.multipleSelection = val.map(v => v.id)
        },
        // 
        handleCreate() {
            this.formVisibleAdd = true
            this.dialogForm = {}

        },
        // 
        handleEdit(row) {
            this.dialogForm = JSON.parse(JSON.stringify(row))
            this.formVisibleEdit = true

        },
        cancel() {
            this.$message.success('');
        },
        // 
        handleDelete(index, row) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                Request.delete("/menu/deleteById/" + row.id).then(response => {
                    if (response.code == 0) {
                        this.$message({
                            type: 'success',
                            message: '!'
                        })
                        this.onReset()
                    } else {
                        this.$message({
                            type: 'success',
                            errpr: '!'
                        })
                    }
                });

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                })
            })
        },
        addChildMenu(pid) {
            this.formVisibleAdd = true;
            this.dialogForm = {};
            if (pid) {
                this.dialogForm.pid = pid;
            }
        },
        // 
        batchDelete() {
            if (this.multipleSelection.length < 1) {
                this.$message({
                    message: '',
                    type: 'warning'
                })
            } else {
                Request.delete(`/menu/deleteBatch?ids=${this.multipleSelection.join(',')}`).then(res => {
                    if (res.code === '0') {
                        this.$message({
                            showClose: true,
                            message: '',
                            type: 'success'
                        });
                        this.onReset()
                    }
                    else {
                        this.$message({
                            showClose: true,
                            message: res.msg,
                            type: 'error',
                        });
                    }
                })

            }
        },
        loadIconList() {

            Request.get("/icon-dict/all").then(response => {
                if (response.code === '0') {
                    this.iconDict = response.data;

                } else {
                    this.$message({
                        type: 'error',
                        message: '!'
                    })
                }
            });

        },
        // 
        fetchData() {
            this.listLoading = true
            this.loadIconList();
            Request.get("/menu/findAll", {
                params: {
                    name: this.listQuery.name
                }
            }).then(response => {
                const data = response.data;
                this.tableData = data;
                this.total = data.length;
            })
            this.listLoading = false
        },
        // 
        onSubmit() {
            this.fetchData()
        },
        onReset() {
            this.listQuery.name = "";
            this.fetchData()
        },


        handleAdd(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    Request.post("/menu/save", this.dialogForm).then(response => {
                        if (response.code == 0) {
                            this.$message({
                                showClose: true,
                                message: '',
                                type: 'success',
                            });
                        } else {
                            this.$message({
                                showClose: true,
                                message: '',
                                type: 'error',
                            });
                        }

                    });
                    this.formVisibleAdd = false
                    return true;
                } else {
                    this.isSubmit = false
                    return false
                }
            })
        },
        handleEditSave(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    Request.put("/menu/" + this.dialogForm.id, this.dialogForm).then(response => {
                        if (response.code == 0) {
                            this.$message({
                                showClose: true,
                                message: '',
                                type: 'success',
                            });
                            this.formVisibleEdit = false
              
                            this.fetchData()
                        } else {
                            this.$message({
                                showClose: true,
                                message: '',
                                type: 'error',
                            });
                        }
                    });

                } else {

                }
            });
        },

        // --excle
        exportTable(type) {
            if (this.tableData.length) {
                const params = {
                    header: ['ID', '', '', '', '', 'ID', '', '', ''], // 
                    key: ['id', 'name', 'path', 'icon', 'description', 'pid', 'pagePath', 'sortNum', 'role'], // key
                    data: this.tableData, // this.tableData
                    autoWidth: true,
                    fileName: '',
                    bookType: type
                }
                excel.exportDataToExcel(params)
                this.exportVisible = false
            }
        },
        getMenuRole(role) {
            switch (role) {
                case 0:
                    return '';
                case 2:
                    return '';
                case 1:
                    return '';
                case 3:
                    return '';
                default: return '';
            }
        }
    }
}
</script>

