import * as XLSX from 'xlsx'
import { saveAs } from 'file-saver'

// 
function AutoWidth(ws, arr) {
  // worksheet
  const colWidth = arr.map(row => row.map(val => {
    // null/undefined
    if (val == null) {
      return { wch: 10 }
    } else if (val.toString().charCodeAt(0) > 255) { // 
      return { wch: val.toString().length * 2 }
    } else {
      return { wch: val.toString().length }
    }
  }))
  // 
  const result = colWidth[0]
  for (let i = 1; i < colWidth.length; i++) {
    for (let j = 0; j < colWidth[i].length; j++) {
      if (result[j].wch < colWidth[i][j].wch) {
        result[j].wch = colWidth[i][j].wch
      }
    }
  }
  ws['!cols'] = result
}

// JSON
function formatJSON(key, data) {
  return data.map(v => key.map(i => { return v[i] }))
}

// ArrayBuffer
function s2ab(s) {
  const buf = new ArrayBuffer(s.length)
  const view = new Uint8Array(buf)
  for (let i = 0; i !== s.length; ++i) view[i] = s.charCodeAt(i) & 0xFF
  return buf
}

// EXCEL
export function exportDataToExcel(
  {
    header, // 
    key, // 
    data, // 
    fileName, // 
    autoWidth = true, // 
    bookType = 'xlsx' // 
  } = {}) {
  // Workbook
  const wb = XLSX.utils.book_new()
  const arr = formatJSON(key, data)
  fileName = fileName || 'excel-list'
  arr.unshift(header)
  // worksheet
  const ws = XLSX.utils.aoa_to_sheet(arr)
  if (autoWidth) {
    AutoWidth(ws, arr)
  }
  // WorkbookworksheetfileName
  XLSX.utils.book_append_sheet(wb, ws, fileName)
  // EXCEL
  const wbout = XLSX.write(wb, {
    bookType: bookType,
    bookSST: false,
    type: 'binary'
  })
  // 
  saveAs(new Blob([s2ab(wbout)], {
    type: 'application/octet-stream'
  }), `${fileName}.${bookType}`)
}

// Excel
function getHeaderRow(sheet) {
  const headers = []
  //  A1:G8 
  const range = XLSX.utils.decode_range(sheet['!ref'])
  let C
  const R = range.s.r
  // 
  for (C = range.s.c; C <= range.e.c; ++C) {
    //  A1 
    const cell = sheet[XLSX.utils.encode_cell({ c: C, r: R })]
    // 
    let hdr = 'UNKNOWN ' + C
    if (cell && cell.t) hdr = XLSX.utils.format_cell(cell)
    headers.push(hdr)
  }
  return headers
}

// Excel
export function readDataFromExcel(data, type) {
  // ExcelWorkbook
  const workbook = XLSX.read(data, { type: type })
  const firstSheetName = workbook.SheetNames[0]
  // Workbookworksheet
  const worksheet = workbook.Sheets[firstSheetName]
  const header = getHeaderRow(worksheet)
  // worksheet
  const results = XLSX.utils.sheet_to_json(worksheet)
  return { header, results }
}

export default {
  exportDataToExcel,
  readDataFromExcel
}
